<template>
    <div class="banner">
        <div class="order_search">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item>
                    <el-input v-model="formInline.searchIup" placeholder="请输入内容"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                    <el-button type="primary" @click="addBanner">增加</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="banner_list">
            <el-table
                :data="tableData"
                style="width: 100%">
                <el-table-column
                type="index"
                :index="indexMethod">
                </el-table-column>
                <el-table-column
                prop="id"
                label="ID"
                width="180">
                </el-table-column>
                <el-table-column
                prop="date"
                label="日期"
                width="180">
                </el-table-column>
                <el-table-column
                prop="name"
                label="操作人"
                width="180">
                </el-table-column>
                <el-table-column
                prop="activity"
                label="活动">
                </el-table-column>
                <el-table-column
                prop="imgUrl"
                label="地址">
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row.id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,15,20]"
                    :page-size="pagesize"
                    :total="total"    
                    layout="total, sizes, prev, pager, next, jumper">    
            </el-pagination>
        </div>



        <el-dialog title="增加广告" :visible.sync="dialogFormVisible">
            <el-form :model="form" label-position="left">
                <el-form-item label="操作人" :label-width="formLabelWidth">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="日期" :label-width="formLabelWidth">
                    <div class="block">
                        <el-date-picker
                        v-model="form.date"
                        type="date"
                        placeholder="选择日期">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item label="活动名称" :label-width="formLabelWidth">
                    <el-input v-model="form.activity" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="上传图片" :label-width="formLabelWidth">
                    <el-upload
                        class="upload-demo"
                        action="/api/banner/upload"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        :before-remove="beforeRemove"
                        :on-success="handleSuccess"
                        multiple   
                        :limit="1"
                        :on-exceed="handleExceed"
                        :file-list="fileList">
                        <el-button size="small" type="primary">点击上传</el-button>
                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                </el-form-item>
                
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="bindClickQue">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<style lang="scss" scoped>
    .page {
        padding: 30px 0;
        float: right;
    }
</style>


<script>
    export default {
        data() {
            return {
                currentPage:1, //初始页
                pagesize:20,    //    每页的数据
                total:3, // 订单列表总数量
                formLabelWidth:'100px',
                dialogFormVisible: false,
                fileList: [],
                form:{
                    date:"",
                    name:"",
                    activity:"",
                    imgUrl:""
                },
                formInline:{
                    searchIup:"1231",
                    
                },
                tableData: [
                    {
                        id:"100001",
                        date: '2016-05-03',
                        name: '九八七',
                        activity: '正月十五元宵节',
                        imgUrl: 'www.baidu.com'
                    },
                    {
                        id:"100001",
                        date: '2016-05-03',
                        name: '九八七',
                        activity: '正月十五元宵节',
                        imgUrl: 'www.baidu.com'
                    },
                    {
                        id:"100001",
                        date: '2016-05-03',
                        name: '九八七',
                        activity: '正月十五元宵节',
                        imgUrl: 'www.baidu.com'
                    }
                ],
            }
        },
        mounted() {
            //this.orderList();   
            this.PageOrderList(); // 分页订单列表
            this.roles = this.$store.state.userInfo.roles;
        },
        created() {
            
        },
        methods: {
            // 初始页currentPage、初始每页数据数pagesize和数据data
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.PageOrderList(); // 分页订单列表
                console.log(this.pagesize)  //每页下拉显示数据
            },
            handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                this.PageOrderList(); // 分页订单列表
                console.log(this.currentPage)  //点击第几页
            },
            PageOrderList () {
                let that = this;
                let url = '/api'+this.$common.bannerPage;
                let data = {
                    offset:that.currentPage,
                    limit:that.pagesize
                }
                that.$http.post(url,data).then((res)=>{
                    console.log(res)
                    if ( res.code == '200' ) {
                        that.tableData = res.data;
                        that.total = res.num;
                    }
                }).catch(data=>{

                })
            },
            indexMethod(index) {
                return index * 1;
            },
            handleDelete(index, id) {
                let that = this;
                let url = '/api'+this.$common.bannerDelete;
                let data = {
                    id:id
                }
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if ( res.status == '1' ) {
                        that.$message({
                            message: '删除成功成功',
                            type: 'success'
                        });
                        that.PageOrderList();
                    }
                }).catch(data=>{

                })
            },
            addBanner () {
                this.dialogFormVisible = true;
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file, fileList) {
                return this.$confirm(`确定移除 ${ file.name }？`);
            },
            handleSuccess (file, fileList) {
                console.log(file)
                this.form.imgUrl = file.imgName;
            },
            onSubmit () {
                this.$message('该功能模块没有开通');
            },
            bindClickQue () {
                let that = this;
                let url = '/api'+that.$common.bannerAdd;
                let data = {
                    name:that.form.name,
                    date:that.form.date,
                    activity:that.form.activity,
                    imgUrl:'/public/images/'+that.form.imgUrl
                }
                that.$http.post(url,data).then((res)=>{
                    console.log(res)
                    if ( res.status == '1' ) {
                        that.dialogFormVisible = false;
                        that.$message({
                            message: '增加接单人成功',
                            type: 'success'
                        });
                        that.PageOrderList();
                    }
                }).catch(data=>{

                })
            }
        }
    }
</script>



